.transition-5s{
    transition: all .5s;
}

.cursor-pointer{
    cursor: pointer;
}

.light-hover {
    color:$light;
    &:hover{
        color:$hover-color;
    }
}

// 
.wrap-square{
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    overflow: hidden;
}
// 
.square-img{
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50% ,-50%);
}

.pointer-event-none{
    pointer-events: none;/*  icon  */
}